import React, { useState } from "react";
import classnames from "classnames";
import axios from "axios";

function CreateAdForm() {
  const [make, setMake] = useState("");
  const [model, setModel] = useState("");
  const [yor, setYor] = useState("");
  const [mileage, setMileage] = useState("");
  const [engine, setEngine] = useState("");
  const [condition, setCondition] = useState("");
  const [bodyType, setBodyType] = useState("");
  const [transmission, setTransmission] = useState("");
  const [fuelType, setFuelType] = useState("");
  const [adDesc, setAdDesc] = useState("");
  const [color, setColor] = useState("");
  // const [img, setImg] = useState("");
  const [images, setImages] = useState("");
  const [price, setPrice] = useState("");
  const [district, setDistrict] = useState("");
  const [city, setCity] = useState("");
  const [name, setName] = useState("");

  const [makeErr, setMakeErr] = useState("");
  const [modeErr, setModeErr] = useState("");
  const [yorErr, setYorErr] = useState("");
  const [engineErr, setEngineErr] = useState("");
  const [mileageErr, setMileageErr] = useState("");
  const [conditionErr, setConditionErr] = useState("");
  const [bodyTypeErr, setBodyTypeErr] = useState("");
  const [transmissionErr, setTransmissionErr] = useState("");
  const [fuelTypeErr, setFuelTypeErr] = useState("");
  const [colorErr, setColorErr] = useState("");
  const [priceErr, setPriceErr] = useState("");
  const [districtErr, setDistrictErr] = useState("");
  const [cityErr, setCityErr] = useState("");
  const [nameErr, setNameErr] = useState("");

  const [loading, setLoading] = useState(false);

  const handleChangeFile = (e) => {
    console.log(e.target.files[0]);
    setImages(e.target.files[0]);

    console.log(images);
  };

  const handleChange = (e) => {
    if (e.target.name === "make") {
      setMake(e.target.value);
      setMakeErr("");
    } else if (e.target.name === "model") {
      setModel(e.target.value);
      setModeErr("");
    } else if (e.target.name === "yor") {
      setYor(e.target.value);
      setYorErr("");
    } else if (e.target.name === "mileage") {
      setMileage(e.target.value);
      setYorErr("");
    } else if (e.target.name === "engine") {
      setEngine(e.target.value);
      setEngineErr("");
    } else if (e.target.name === "condition") {
      setCondition(e.target.value);
      setEngineErr("");
    } else if (e.target.name === "bodyType") {
      setBodyType(e.target.value);
      setBodyTypeErr("");
    } else if (e.target.name === "transmission") {
      setTransmission(e.target.value);
      setTransmissionErr("");
    } else if (e.target.name === "transmission") {
      setFuelType(e.target.value);
      setFuelTypeErr("");
    } else if (e.target.name === "color") {
      setColor(e.target.value);
      setColorErr("");
    } else if (e.target.name === "adDesc") {
      setAdDesc(e.target.value);
    } else if (e.target.name === "district") {
      setDistrict(e.target.value);
      setDistrictErr("");
    } else if (e.target.name === "city") {
      setCity(e.target.value);
      setCityErr("");
    } else if (e.target.name === "name") {
      setName(e.target.value);
      setNameErr("");
    }
  };

  // validate
  const validate = () => {
    let makeErr = "";
    let modeErr = "";
    let yorErr = "";
    let mileageErr = "";
    let engineErr = "";
    let conditionErr = "";
    let bodyTypeErr = "";
    let transmissionErr = "";
    let fuelTypeErr = "";
    let colorErr = "";
    let districtErr = "";
    let cityErr = "";
    let nameErr = "";

    if (!make) {
      makeErr = "Make is required";
    }

    if (!model) {
      modeErr = "Model is required";
    }

    if (!yor) {
      yorErr = "Year of registration is required";
    }
    if (!mileage) {
      mileageErr = "Mileage is required";
    }

    if (!engine) {
      engineErr = "Engine is required";
    }

    if (!condition) {
      conditionErr = "Condition is required";
    }

    if (!bodyType) {
      bodyTypeErr = "Body type is required";
    }

    if (!transmission) {
      transmissionErr = "Transmission is required";
    }

    if (!fuelType) {
      fuelTypeErr = "Fuel type is required";
    }

    if (!color) {
      colorErr = "Color is required";
    }

    if (!district) {
      districtErr = "District is required";
    }

    if (!city) {
      cityErr = "City is required";
    }

    if (!name) {
      nameErr = "Contact name is required";
    }

    if (
      makeErr ||
      modeErr ||
      yorErr ||
      mileageErr ||
      engineErr ||
      conditionErr ||
      bodyTypeErr ||
      transmissionErr ||
      fuelTypeErr ||
      colorErr ||
      districtErr ||
      cityErr ||
      nameErr
    ) {
      setMakeErr(makeErr);
      setModeErr(modeErr);
      setYorErr(yorErr);
      setMileageErr(mileageErr);
      setEngineErr(engineErr);
      setConditionErr(conditionErr);
      setBodyTypeErr(bodyTypeErr);
      setTransmissionErr(transmissionErr);
      setFuelTypeErr(fuelTypeErr);
      setColorErr(colorErr);
      setDistrictErr(districtErr);
      setCityErr(cityErr);
      setNameErr(nameErr);

      return false;
    }

    return true;
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (validate()) {
      setLoading(true);
    }
  };

  return (
    <div className="row m-0">
      <div className="col p-4">
        <form noValidate>
          <div className="ad-form-sec">
            <div class="form-row header">
              <div className="form-group col px-sm-2 mb-0">
                <p className="heading mb-0">Basic Details </p>
                <p className="info-desc">All fields are mandatory.</p>
              </div>
            </div>

            <div className="content">
              <div class="form-row">
                <div className="form-group col-md-6 px-sm-2 mb-4">
                  <label htmlFor="make">
                    Make <span className="required-tag">*</span>
                  </label>
                  <select
                    className={classnames("form-control", {
                      "is-invalid": makeErr,
                    })}
                    id="make"
                    name="make"
                    onChange={handleChange}
                    value={make}
                  >
                    <option selected>-- Select --</option>
                    <option>Audi</option>
                    <option>Benz</option>
                  </select>
                  <div className="invalid-feedback">{makeErr}</div>
                </div>

                <div className="form-group col-md-6 px-sm-2 mb-4">
                  <label htmlFor="model">
                    Model <span className="required-tag">*</span>
                  </label>
                  <select
                    className={classnames("form-control", {
                      "is-invalid": modeErr,
                    })}
                    id="model"
                    name="model"
                    onChange={handleChange}
                    value={model}
                  >
                    <option selected>-- Select --</option>
                    <option>Audi</option>
                    <option>Benz</option>
                  </select>
                  <div className="invalid-feedback">{modeErr}</div>
                </div>

                <div className="form-group col-md-6 px-sm-2 mb-4">
                  <label htmlFor="yor">
                    Year of Registration <span className="required-tag">*</span>
                  </label>
                  <input
                    type="number"
                    className={classnames("form-control", {
                      "is-invalid": yorErr,
                    })}
                    id="yor"
                    name="yor"
                    onChange={handleChange}
                    value={yor}
                  />
                  <div className="invalid-feedback">{yorErr}</div>
                </div>

                <div className="form-group col-md-6 px-sm-2 mb-4">
                  <label htmlFor="mileage">
                    Mileage (km) <span className="required-tag">*</span>
                  </label>
                  <input
                    type="number"
                    className={classnames("form-control", {
                      "is-invalid": mileageErr,
                    })}
                    id="mileage"
                    name="mileage"
                    onChange={handleChange}
                    value={mileage}
                  />
                  <div className="invalid-feedback">{mileageErr}</div>
                </div>

                <div className="form-group col-md-6 px-sm-2 mb-4">
                  <label htmlFor="engine">
                    Engine cc <span className="required-tag">*</span>
                  </label>
                  <input
                    type="number"
                    className={classnames("form-control", {
                      "is-invalid": engineErr,
                    })}
                    id="engine"
                    name="engine"
                    onChange={handleChange}
                    value={engine}
                  />
                  <div className="invalid-feedback">{engineErr}</div>
                </div>
                <div className="form-group col-md-6 px-sm-2 mb-4">
                  <label htmlFor="condition">
                    Condition <span className="required-tag">*</span>
                  </label>
                  <select
                    className={classnames("form-control", {
                      "is-invalid": conditionErr,
                    })}
                    id="condition"
                    name="condition"
                    onChange={handleChange}
                    value={condition}
                  >
                    <option selected>-- Select --</option>
                    <option>Audi</option>
                    <option>Benz</option>
                  </select>
                  <div className="invalid-feedback">{conditionErr}</div>
                </div>
                <div className="form-group col-md-6 px-sm-2 mb-4">
                  <label htmlFor="bodyType">
                    Body type <span className="required-tag">*</span>
                  </label>
                  <select
                    className={classnames("form-control", {
                      "is-invalid": bodyTypeErr,
                    })}
                    id="bodyType"
                    name="bodyType"
                    onChange={handleChange}
                    value={bodyType}
                  >
                    <option selected>-- Select --</option>
                    <option>Audi</option>
                    <option>Benz</option>
                  </select>
                  <div className="invalid-feedback">{bodyTypeErr}</div>
                </div>
                <div className="form-group col-md-6 px-sm-2 mb-4">
                  <label htmlFor="transmission">
                    Transmission <span className="required-tag">*</span>
                  </label>
                  <select
                    className={classnames("form-control", {
                      "is-invalid": transmissionErr,
                    })}
                    id="transmission"
                    name="transmission"
                    onChange={handleChange}
                    value={transmission}
                  >
                    <option selected>-- Select --</option>
                    <option>Audi</option>
                    <option>Benz</option>
                  </select>
                  <div className="invalid-feedback">{transmissionErr}</div>
                </div>
                <div className="form-group col-md-6 px-sm-2 mb-4">
                  <label htmlFor="fuelType">
                    Fuel Type <span className="required-tag">*</span>
                  </label>
                  <select
                    className={classnames("form-control", {
                      "is-invalid": fuelTypeErr,
                    })}
                    id="fuelType"
                    name="fuelType"
                    onChange={handleChange}
                    value={fuelType}
                  >
                    <option selected>-- Select --</option>
                    <option>Audi</option>
                    <option>Benz</option>
                  </select>
                  <div className="invalid-feedback">{fuelTypeErr}</div>
                </div>
                <div className="form-group col-md-6 px-sm-2 mb-4">
                  <label htmlFor="color">
                    Color <span className="required-tag">*</span>
                  </label>
                  <select
                    className={classnames("form-control", {
                      "is-invalid": colorErr,
                    })}
                    id="color"
                    name="color"
                    onChange={handleChange}
                    value={color}
                  >
                    <option selected>-- Select --</option>
                    <option>Audi</option>
                    <option>Benz</option>
                  </select>
                  <div className="invalid-feedback">{colorErr}</div>
                </div>
              </div>
            </div>
          </div>

          <div className="ad-form-sec">
            <div class="form-row header">
              <div className="form-group col px-sm-2 mb-0">
                <p className="heading mb-0">
                  Photos <span className="required-tag">*</span>
                </p>
                <p className="info-desc">
                  Minimum 1 and maximum 5 photos are allowed.
                </p>
              </div>
            </div>
            <div className="content">
              <div class="form-row">
                <div className="form-group col px-sm-2 mb-4">
                  {images && (
                    <div className="img-upload">
                      <img src={URL.createObjectURL(images)} alt="" />
                    </div>
                  )}

                  <label htmlFor="img-upload" className="img-upload">
                    <i class="fas fa-plus"></i>
                  </label>
                  <input
                    type="file"
                    className="form-control"
                    id="img-upload"
                    name="img"
                    // value={img}
                    onChange={handleChangeFile}
                    rows="5"
                    placeholder="Optional"
                  />
                </div>
              </div>
            </div>
          </div>

          <div className="ad-form-sec">
            <div class="form-row header">
              <div className="form-group col px-sm-2 mb-0">
                <p className="heading mb-0">Ad Description</p>
                <p className="info-desc">Not mandatory but recommended.</p>
              </div>
            </div>
            <div className="content">
              <div class="form-row">
                <div className="form-group col px-sm-2 mb-4">
                  <textarea
                    className="form-control"
                    name="adDesc"
                    onChange={handleChange}
                    value={adDesc}
                    rows="5"
                    placeholder="Optional"
                  />
                </div>
              </div>
            </div>
          </div>

          <div className="ad-form-sec">
            <div class="form-row header">
              <div className="form-group col px-sm-2 mb-0">
                <p className="heading mb-0">Other Features</p>
                <p className="info-desc">
                  Add any amount of features. Not mandatory but recommended.
                </p>
              </div>
            </div>
            <div className="content">
              <div class="form-row">
                <div className="form-group col-md-6 px-sm-2 mb-4">
                  <label htmlFor="color">Feature</label>
                  <input
                    type="text"
                    className="form-control"
                    name="adDesc"
                    onChange={handleChange}
                    value={adDesc}
                    placeholder="Eg: Full option"
                  />
                  <div className="invalid-feedback">{priceErr}</div>
                  <button
                    type="submit"
                    className="btn-submit mt-3"
                    onClick={handleSubmit}
                    style={{ width: 70, marginRight: 12 }}
                  >
                    {loading ? (
                      <div
                        className="spinner-border spinner-border-sm text-light"
                        role="status"
                      >
                        <span className="sr-only">Loading...</span>
                      </div>
                    ) : null}
                    {loading ? "" : "Add"}
                  </button>
                </div>
              </div>
            </div>
          </div>

          <div className="ad-form-sec">
            <div class="form-row header">
              <div className="form-group col px-sm-2 mb-0">
                <p className="heading mb-0">
                  Price <span className="required-tag">*</span>
                </p>
                <p className="info-desc">
                  You can set either price or negotiable
                </p>
              </div>
            </div>
            <div className="content">
              <div class="form-row">
                <div className="form-group col-md-6 px-sm-2 mb-4">
                  <div class="input-group mb-2">
                    <div class="input-group-prepend">
                      <div class="input-group-text">Rs.</div>
                    </div>
                    <input
                      type="number"
                      className={classnames("form-control", {
                        "is-invalid": priceErr,
                      })}
                      name="price"
                      onChange={handleChange}
                    />
                  </div>
                  <div class="form-group form-check mt-2 mb-0">
                    <input
                      type="checkbox"
                      class="form-check-input"
                      id="priceCheckbox"
                    />
                    <label class="form-check-label" for="priceCheckbox">
                      Negotiable
                    </label>
                  </div>
                  <div className="invalid-feedback">{priceErr}</div>
                </div>
              </div>
            </div>
          </div>

          <div className="ad-form-sec">
            <div class="form-row header">
              <div className="form-group col px-sm-2 mb-0">
                <p className="heading mb-0">Location</p>
                <p className="info-desc">Where is the car located at?</p>
              </div>
            </div>
            <div className="content">
              <div class="form-row">
                <div className="form-group col-md-6 px-sm-2 mb-4">
                  <label htmlFor="district">
                    District <span className="required-tag">*</span>
                  </label>
                  <select
                    className={classnames("form-control", {
                      "is-invalid": districtErr,
                    })}
                    id="district"
                    name="district"
                    value={district}
                    onChange={handleChange}
                  >
                    <option selected>-- Select --</option>
                    <option>Audi</option>
                    <option>Benz</option>
                  </select>
                  <div className="invalid-feedback">{districtErr}</div>
                </div>
                <div className="form-group col-md-6 px-sm-2 mb-4">
                  <label htmlFor="city">
                    City <span className="required-tag">*</span>
                  </label>
                  <input
                    type="text"
                    className={classnames("form-control", {
                      "is-invalid": cityErr,
                    })}
                    name="city"
                    value={city}
                    onChange={handleChange}
                  />
                  <div className="invalid-feedback">{cityErr}</div>
                </div>
              </div>
            </div>
          </div>

          <div className="ad-form-sec">
            <div class="form-row header">
              <div className="form-group col px-sm-2 mb-0">
                <p className="heading mb-0">
                  Contact Details <span className="required-tag">*</span>
                </p>
                <p className="info-desc">Can add multiple phone numbers.</p>
              </div>
            </div>
            <div className="content">
              <div class="form-row">
                <div className="form-group col-md-6 px-sm-2 mb-4">
                  <label htmlFor="name">
                    Contact Name <span className="required-tag">*</span>
                  </label>
                  <input
                    type="text"
                    className={classnames("form-control", {
                      "is-invalid": nameErr,
                    })}
                    name="name"
                    value={name}
                    onChange={handleChange}
                  />
                  <div className="invalid-feedback">{nameErr}</div>
                </div>
              </div>
              <div class="form-row">
                <div className="form-group col-md-6 px-sm-2 mb-4">
                  <label htmlFor="fuelType">
                    Phone <span className="required-tag">*</span>
                  </label>
                  <div class="input-group mb-2">
                    <div class="input-group-prepend">
                      <div class="input-group-text">+94</div>
                    </div>
                    <input
                      type="number"
                      className={classnames("form-control", {
                        "is-invalid": priceErr,
                      })}
                      name="price"
                      onChange={handleChange}
                      placeholder="Phone 1"
                    />
                  </div>
                  <div class="input-group mb-2">
                    <div class="input-group-prepend">
                      <div class="input-group-text">+94</div>
                    </div>
                    <input
                      type="number"
                      className={classnames("form-control", {
                        "is-invalid": priceErr,
                      })}
                      name="price"
                      onChange={handleChange}
                      placeholder="Phone 2"
                    />
                  </div>
                  <div class="input-group mb-2">
                    <div class="input-group-prepend">
                      <div class="input-group-text">+94</div>
                    </div>
                    <input
                      type="number"
                      className={classnames("form-control", {
                        "is-invalid": priceErr,
                      })}
                      name="price"
                      onChange={handleChange}
                      placeholder="Phone 3"
                    />
                  </div>
                  <div className="invalid-feedback">{priceErr}</div>
                </div>
              </div>
            </div>
            <button
              type="submit"
              className="btn-submit mt-3"
              onClick={handleSubmit}
              style={{ width: 150, float: "right" }}
            >
              {loading ? (
                <div
                  className="spinner-border spinner-border-sm text-light"
                  role="status"
                >
                  <span className="sr-only">Loading...</span>
                </div>
              ) : null}
              {loading ? "" : "Post Ad"}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}

export default CreateAdForm;
